import React from 'react';
import { IIconButtonProps } from './interface';
import { BaseIconButton } from './BaseIconButton';
import { useToolbarItemConfig } from '../common/hooks/useToolbarItemConfig'

export type { IIconButtonProps as ITBIconButtonProps } from './interface';

export const TBIconButton = React.memo(React.forwardRef<HTMLDivElement, IIconButtonProps>(
  (props, ref) => {

    const { itemSize } = useToolbarItemConfig();

    return (
      <BaseIconButton
        size={itemSize}
        ref={ref}
        {...props}
      />
    );
  },
));

TBIconButton.displayName = 'iconButton';
